.second-level-banner{
    padding: 0 ;
    @include desktop-down{
      padding: 0;
    }
    .container{
      padding: 0;
      max-width: 1920px;
    }
  }
  .second-level-banner__column-wrapper{
    display: flex;
    width: 100%;
    height: 352px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    @include desktop-down{
      flex-direction: column-reverse;
      flex-wrap: nowrap;
      height: 100%;
    }
  }
.second-level-banner--img-wrapper{
    width: 63.11%;
    height: 320px;
    @include desktop-down{
      width: 100%;
    }
    @include phone-down{
      height: 193px;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
.second-level-banner--content-wrapper{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 36.89%;
    height: 100%;
    background: #1B252C 0% 0% no-repeat padding-box;
    @include desktop-down{
      position: absolute;
      bottom: -69px;
      width: calc(100% - 32px);
      margin: 0 16px;
      display: block;
      height: auto;
      padding: 41px 0;
    }
    h1{
      width: 100%;
      max-width: 300px;
      margin: auto;
      color: $white;
      align-self: center;
      text-align: center;
      position: relative;
      @include desktop {
        font-size: $font-size-40;
      }
      @include tablet-down{
        font-size: $font-size-36;
        line-height: $line-spacing-38;
      }

      &::before{
        content: "";
        position: absolute;
        width: 102px;
        height: 8px;
        background-color: $primary-brand;
        left: 50%;
        bottom: -32px;
        transform: translateX(-50%);
        @include desktop-down{
          bottom: -15px;
        }
      }
    }
  }
