.kart-btn{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 318px;
  text-transform: capitalize;
  color: $white;
  background-color: $primary-brand;
  padding: 14px 0 9px;
  cursor: pointer;
  span{
    font-size: 14px;
    line-height: $line-spacing-16;
    font-weight: bold;
  }
}
